<template>
  <v-container>
    <template>
      <v-form
        ref="form"
        v-model="valid"
        @submit.prevent="validate"
        lazy-validation
        autocomplete="off"
      >
        <div>
          <v-row>
            <v-col cols="12" md="8">
              <v-row id="form-content">
                <v-col cols="12">
                  <h2 class="text-center" id="title1">
                    CONCTA<span style="color: #5db88a">CTAME</span>
                  </h2>
                </v-col>
                <v-col cols="12" md="8">
                  <v-text-field
                    v-model="name"
                    :rules="nameRules"
                    label="Nombre"
                    required
                    solo
                    name="name"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" md="4">
                  <v-text-field
                    v-model="phone"
                    solo
                    :rules="nameRules"
                    label="Telefono"
                    required
                    name="phone"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    v-model="email"
                    :rules="emailRules"
                    label="E-mail"
                    solo
                    required
                    name="email"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-textarea
                    label="Mensaje"
                    solo
                    :rules="messageRules"
                    v-model="message"
                    name="message"
                  >
                  </v-textarea>
                </v-col>
                <v-col cols="12">
                  <v-btn
                    :loading="loading"
                    :disabled="!valid"
                    color="teal darken-1"
                    dark
                    width="250"
                    class="px-5"
                    type="submit"
                    value="Send"
                  >
                    Enviar
                  </v-btn>
                </v-col>
                <v-col cols="12">
                  <div id="shape1"></div>
                  <div id="shape2"></div>
                  <div id="shape3"></div>
                  <div id="shape4"></div>
                  <div id="shape5"></div>
                </v-col>
              </v-row>
            </v-col>
          </v-row>
        </div>
      </v-form>
    </template>
  </v-container>
</template>

<script>
import emailjs from "emailjs-com";

export default {
  name: "Contact",
  data: () => ({
    loading: false,
    valid: true,
    name: "",
    nameRules: [(v) => !!v || "Nombre es requerido"],
    phone: "",
    phoneRules: [(v) => !!v || "Telefono es requerido"],
    email: "",
    emailRules: [
      (v) => !!v || "E-mail es requerido",
      (v) => /.+@.+\..+/.test(v) || "E-mail debe ser uno valido",
    ],
    message: "",
    messageRules: [(v) => !!v || "Mensaje es requerido"],
  }),

  methods: {
    sendEmail(e) {
      this.loading = true;
      setTimeout(() => {
        try {
          emailjs.sendForm(
            "service_bppklbx",
            "template_ryxjnim",
            e.target,
            "user_pjKZVdCludi6ZhH3XLxIn",
            {
              name: this.name,
              email: this.email,
              phone: this.phone,
              message: this.message,
            }
          );
          this.name = "";
          this.email = "";
          this.phone = "";
          this.message = "";
          this.$refs.form.reset();
          this.loading = false;
          alert("Mensaje enviado, gracias por contactarme.");
          // this.$refs.form.resetValidation();
        } catch (error) {
          this.loading = false;
          alert("Estamos presentando un problema, intente en otro momento.");
        }
      }, 3000);
    },
    validate(e) {
      if (this.$refs.form.validate()) {
        this.sendEmail(e);
      } else {
        alert("Por favor llena el formulario.");
      }
    },
  },
};
</script>

<style scoped>
#title1 {
  font-size: 32px;
  font-weight: 400;
  margin-top: 10px;
  margin-bottom: 40px;
}
#form-content {
  border: 2px solid #ececec;
  border-radius: 15px;
  padding: 25px;
  margin-top: 45px;
}
#shape1 {
  position: absolute;
  /* z-index: 1; */
  top: 2641px;
  left: 750px;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background: #39f89f2e;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}
#shape3 {
  position: absolute;
  /* z-index: 1; */
  top: 2595px;
  left: -33px;
  width: 280px;
  height: 280px;
  border-radius: 50%;
  background: #39f89f2e;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}
#shape4 {
  position: absolute;
  z-index: 2;
  top: 2889px;
  left: 600px;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background: #39f89f2e;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}
#shape5 {
  position: absolute;
  /* z-index: 1; */
  top: 3170px;
  left: 106px;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: #39f89f2e;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}
#shape2 {
  position: absolute;
  /* z-index: 1; */
  top: 2669px;
  left: 777px;
  width: 145px;
  height: 145px;
  border-radius: 50%;
  background: #ffffff;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}

@media only screen and (max-width: 600px) {
  #title1 {
    font-size: 25px;
    font-weight: 400;
    margin-top: 10px;
    margin-bottom: 40px;
  }
  #shape1 {
    width: 0px;
    height: 0px;
  }
  #shape3 {
    width: 0px;
    height: 0px;
  }
  #shape4 {
    width: 0px;
    height: 0px;
  }
  #shape5 {
    width: 0px;
    height: 0px;
  }
  #shape2 {
    width: 0px;
    height: 0px;
  }
}
</style>
